import React, { useState, useContext } from 'react'
import { ConfigContext } from '../../config.context';
import toggleIconSVG from './toggle.svg'
import toggleDownIconSVG from './toggleDown.svg';

const SidebarColors = ({ colorsProp, handleChange, title = '',  defaultToggle = false, toggleIcon = toggleIconSVG, toggleDownIcon = toggleDownIconSVG ,  closable = true }) => {
    const [config, setConfig] = useContext(ConfigContext);

    const [closed, setClosed] = useState(defaultToggle);
    

    const handleToggle = () => setClosed(!closed);

    const colr = ['Space Grey', 'Space Green', 'Black Matt', 'Silver', 'Gold', 'Rose Gold', 'Blue', 'Purple', 'Yellow', 'Product RED' ];
    const [colors, setColors] = useState(colr.reduce((acc, curr) => ({ ...acc, [curr]: { value: curr, checked: false  } }) ))
    console.log(colors);

    const handleOnChange = e => {
    }

    return (
        <div className="color side-el">
            <div className="title">
                {
                    Object.keys(title).indexOf(config.language) > -1 ? title[config.language] : 
                    title.US != undefined ? title.US : 'Untitled'
                }

                {closable ? (
                        <img src={ closed === true ? toggleDownIcon : toggleIcon }
                            onClick={handleToggle} 
                            className="pointer" 
                            alt="toggleIcon" 
                        />
                    ) : ''}
                
            </div>

            {/* {
                closed === false && colors != undefined ?  (
                        <div className="side-items">
                            {
                                colors.map((color, i) => (
                                    <label key={i}>
                                        <input onChange={handleOnChange} className="side-el-check" type="checkbox" name="color"  value={color.value} checked={color.checked}/>
                                        <span className="side-el-"></span>
                                        <span className="side-el-text">{color.color}</span>
                                    </label>
                                ))
                            }
                        </div>
                ) : ""
            } */}
            
        </div>
    )
}

export default SidebarColors
